import React from "react"
// import { css } from "@emotion/core"
import { graphql } from "gatsby"
// import { rhythm } from "../../utils/typography"
import Layout from "../../components/layout"


import Collapse from '@material-ui/core/Collapse'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'



const IndexPage = ({ data }) => {

  const [week, showSchedule] = React.useState(true)
  const [info, setInfo] = React.useState(true)
      // const post = data.markdownRemark

 
  function handleClick(id) {
    switch(id) {
      case "week":
        showSchedule(!week)
        break;
      case "info":
        setInfo(!info)
        break
    }
  }



  return (

    <Layout>
        <h1>
            <p>Typography 3</p>
            <p>GRAPH-3223-05 (29748), Fall 2019 <br />
            Thursday, 1:10pm – 6:10pm, RISD <br />
            DC 210, 30 N Main St, Providence, RI 02903 <br />
            Instructor: Minkyoung Kim, mkim37@risd.edu <br />
            TA: Seyong Ahn, sahn04@risd.edu <br />
            Office hours: By appointment, Friday 9am – Noon</p>
        </h1>
      <div className="accordion-list">
        {data.allMarkdownRemark.edges.map(({ node }) => (
          <div key={node.id}>
              <ListItem id={node.id} className="accordion" button onClick={() => handleClick("week")}>
                  <ListItemText primary={node.frontmatter.date} className="accordion-heading  maxw-20" /> {node.frontmatter.title}
              </ListItem>
              <Collapse in={!week} timeout="auto"  unmountOnExit>
                <List component="div" disablePadding>
                <div className="accordion-content" dangerouslySetInnerHTML={{ __html: node.html }} />
                </List>
              </Collapse>
          </div>
        ))}
      </div>
    </Layout>
  )
}

export default IndexPage




export const query = graphql`
  query {
    allMarkdownRemark(
        sort: { fields: [frontmatter___date], order: ASC } 
        filter: {fileAbsolutePath: {regex: "/t3f19/_schedule/.*.md$/"}}
        ) {
      totalCount
      edges {
        node {
          html
          id
          frontmatter {
            title
            date
          }
          fields {
            slug
          }
        }
      }
    }
  }
`